* {
  box-sizing: border-box;
}
.loader__text {
  color: blueviolet;
}
body {
  margin: 0;
  padding: 0;
  background-color: #000000;
  height: 100%;
  font-family: Arial, Helvetica, sans-serif;
  list-style: none;
  text-decoration: none;
}
html,
body,
.container {
  width: 100%;
}
.movies {
  display: grid;
  grid-template-columns: 1fr 1fr;
  width: 100%;
  row-gap: 10px;
  column-gap: 15px;
}
.movies .movie {
  background-color: rgb(255, 255, 88);
  height: 200px;
  display: flex;
  color: rgb(21, 22, 22);
  border-radius: 5px;
}
.movie img {
  position: relative;
  top: 0;
  width: 100px;
  height: 140px;
  margin-right: 20px;
}
.movie h4 {
  font-weight: 400;
  margin: 0;
}
.movie h3 {
  margin-bottom: 7px;
  font-size: 18px;
  font-weight: 700;
  margin-top: 10px;
}

.movie__genres {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
}
.movie__genres li {
  margin-right: 4px;
}
a {
  text-decoration: none;
}
@media(min-width : 950px) {
  .movies {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
  }
}
